<template>
  <div class="area" :style="{ bottom: bottom }">
    <div class="title">输入配送地区<img class="close" src="../assets/close.jpg" @click="close()" /></div>
    <div class="tags">
      <div class="tag" v-for="(li, index) in g" :key="index" @click="tagClick(index)">
        {{ li.nowTag }}
        <div v-if="index === nowIdex" class="bottom-bar animated fadeIn"></div>
      </div>
    </div>
    <div class="divid"></div>
    <div class="lists">
      <div class="list" v-for="(li, index) in nowList" @click="set(li.child, li.name)" :key="index">{{li.name}}<img src="../assets/dui.jpg" class="gouzi" v-if="li.name === g[0].nowTag || (g[1] && li.name === g[1].nowTag) || (g[2] && li.name === g[2].nowTag)"/></div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'areap',
  data () {
    return {
      g: [
        {
          nowTag: '',
          tags: this.$root.area
        }
      ],
      nowList: [],
      nowIdex: 0,
      show: false,
      bottom: '-100vh',
      protect: false
    }
  },
  methods: {
    close () {
      if (this.protect) {
        return
      }
      this.protect = true
      setTimeout(() => {
        this.protect = false
      })
      // eslint-disable-next-line
      this.g[0].nowTag ? this.$parent.inputData.province = this.g[0].nowTag : ''
      // eslint-disable-next-line
      this.g[1] && this.g[1].nowTag ? this.$parent.inputData.city = this.g[1].nowTag : ''
      // eslint-disable-next-line
      this.g[2] && this.g[2].nowTag ? this.$parent.inputData.area = this.g[2].nowTag : ''

      if (this.show) {
        this.show = false
        this.bottom = '-100vh'
        // eslint-disable-next-line
        this.$parent.show ? this.$parent.show = false : ''
      } else {
        this.show = true
        this.bottom = '0px'
      }
    },
    tagClick (index) {
      this.nowIdex = index
      this.nowList = this.g[index].tags
    },
    set (area, name) {
      console.log(name)
      this.g[this.nowIdex].nowTag = name
      if (area) {
        this.g.splice(this.nowIdex + 1)
        this.g.push({
          nowTag: '',
          tags: area
        })
        this.nowIdex += 1
        this.nowList = area
      }
    }
  },
  mounted () {
    this.nowList = this.$root.area
  }
}
</script>

