import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import './registerServiceWorker'
import WJH from '@/js/wjhJS.js'
import { callAppRouter } from '@/js/wjhJS.js'
import animated from 'animate.css'
import Share from '@/js/shareAndGetName'
import Tool from './js/tool.js'
import Axios from 'axios'

const area = require('./json/area.json')

console.log(process.env.NODE_ENV, process.env.VUE_APP_TITLE)

const URL = process.env.NODE_ENV === 'production' && process.env.VUE_APP_TITLE !== 'experiment' ? 'https://api.psy-1.com' : 'https://api.debug.psy-1.com'

const myAxios = Axios.create({
  baseURL: URL
})

Vue.prototype.WJH = WJH
Vue.prototype.$axios = myAxios

Vue.config.productionTip = false
require('@/js/setSize')
Vue.use(animated)

console.log(
  process.env
)

new Vue({
  router,
  store,
  data () {
    return {
      url: URL,
      openid: '',
      gift_id: WJH.parseQuery(document.URL).gift_id,
      sourceData: {
        func_id: '',
        func_type: '',
        gift_count_remain: '',
        gift_icon: '',
        gift_index: '',
        gift_point: '',
        gift_shell_count: '',
        gift_type: '',
        gift_vip_time: ''
      },
      area
    }
  },
  methods: {
    getOpenid () {
      callAppRouter('Login', {}, (res, ed) => {
        let mesg = null
        if (typeof ed === 'string') {
          mesg = JSON.parse(ed)
        } else {
          mesg = ed
        }
        this.openid = mesg.data.openid
        console.log(this.openid)
      })
    },
    getData () {
      this.$axios.get('/web/v1/cosleep/point/gift/detail', { params: { 'gift_id': this.gift_id } }).then(res => {
        if (+res.data.status === 1) {
          this.sourceData = res.data.data
        }
      })
      console.log(this.area, 'area')
    },
    shareM (title = '小睡眠', desc = '', picUrl) {
      // eslint-disable-next-line
      const share = new Share({ pic: picUrl, url: window.location.href.split('#')[0], title: title, desc: desc })
      share.appShare()
      share.rawWeiXinShare(this.url)
    },
    test () {
      if (Tool.is_cosleep()) {
        this.share.appGetName()
      }
      if (/micromessenger/.test(navigator.userAgent.toLowerCase())) {
        this.share.weiXinInit(this.url)
        this.share.weatherCode()
        this.share.weiXinGetName(this.url)
      }
    }
  },
  render: h => h(App),
  mounted () {
    this.shareM('小睡眠', '给你婴儿般的好睡眠', 'http://www.pgyer.com/c5bd0e28109ad23bbc616d3d324405b6')
    this.getData()
    if (Tool.is_cosleep()) {
      this.getOpenid()
    }
  }
}).$mount('#app')
