<template>
  <div class="home">
    <transition name="fade"
                enter-active-class="fadeInDown"
                leave-active-class="fadeOutUp">
      <div class="top-tips animated" v-show="top_tips">请输入完整信息</div>
    </transition>
    <div class="part-1">
      <img class="source-img" :src="this.$root.sourceData.gift_icon"/>
      <div class="source-desc">
        <div class="integration">{{this.$root.sourceData.gift_point}}<span>积分</span></div>
        <div class="left-count">库存：{{this.$root.sourceData.gift_count_remain}}</div>
      </div>
    </div>

    <div class="part-2">
      <div class="input-template"><div class="desc">所在地区：</div><div @click="showArea" :style="{ color: changeColor }" class="my-div">{{ alst(inputData.province + ' ' + inputData.city + ' ' + inputData.area) }}</div></div>
      <div class="input-template"><div class="desc">详细地址：</div><textarea class="my-input textarea" v-model="inputData.address" placeholder="输入详细街道门牌号信息" ></textarea></div>
      <div class="input-template"><div class="desc">收件人：</div><input class="my-input" v-model="inputData.receiver_name" placeholder="输入收件人信息" /></div>
      <div class="input-template"><div class="desc">QQ号：</div><input class="my-input" v-model="inputData.qq" placeholder="输入QQ号码" /></div>
      <div class="input-template"><div class="desc">手机号：</div><input class="my-input" v-model="inputData.mobile" placeholder="输入手机号码" /></div>
      <div class="input-template">
        <div class="desc">数量：</div>
        <div class="counter-template">
          <div class="outer">
            <div class="minus" @click="minus">-</div>
            <div class="csp" v-if="minusDisable"></div>
          </div>
          <div class="number">{{ acceptNumber }}</div>
          <div class="outer">
            <div class="add" @click="add">+</div>
            <div class="csp" v-if="addDisable"></div>
          </div>
        </div>
      </div>
    </div>

    <div style="flex-grow: 1;position: relative;min-height: 20vh">
      <div class="submit" @click="showConfirmMethods">马上兑换</div>
    </div>

    <div class="confirm-template" v-show="showConfirm">
      <div class="confirm">
        <img class="close" @click="showConfirm = false" src="../assets/close.jpg"/>
        <img class="img" :src="this.$root.sourceData.gift_icon" />
        <div class="font">核对订单</div>
        <div class="data">
          <p v-if="inputData.province">地址：{{ alst(inputData.province + inputData.city + inputData.area) + inputData.address }}</p>
          <p v-else>地址：</p>
          <p>收件人：{{ inputData.receiver_name }}</p>
          <p>联系号码：{{ inputData.mobile }}</p>
        </div>
        <div class="desc">·订单信息一旦确认将不可更改·</div>
        <div class="button" @click="submit">确认无误</div>
      </div>
    </div>

    <div class="confirm-template" v-show="tips === 0">
      <div class="confirm">
        <img class="close" @click="tips = 2" src="../assets/close.jpg"/>
        <img class="img" style="border: 0px solid #fff" src="../assets/success.png" />
        <div class="font">兑换成功</div>
        <div class="data">
          恭喜你成功获得礼品，我们将会尽快为你配送，请务必保持手机畅通，以确保礼品的查收。
        </div>
        <div class="desc">
          ·可在兑换记录中查看配送信息·
        </div>
        <div class="button" @click="toJiFen">查看订单状态</div>
      </div>
    </div>

    <div class="confirm-template" v-show="tips === 1">
      <div class="confirm">
        <img class="close" @click="tips = 2" src="../assets/close.jpg"/>
        <img class="img" style="border: 0px solid #fff" src="../assets/fail.png" />
        <div class="font">兑换失败</div>
        <div class="data">
          你当前的积分不足，无法兑换，可通过完成任务领取积分，也可以通过每日签到来赚取积分。开通会员可获得每日签到领取1.2倍积分和每月补签等会员权益。
        </div>
        <div class="button kthy" @click="toHuiYuanCenter">马上开通会员</div>
      </div>
    </div>

    <div class="clasp animated fadeIn" v-if="show">
    </div>
    <AreaP class="area" ref="area"></AreaP>
  </div>
