<script>
// @ is an alias to /src
// .replace(/[^\d.]/g,’’)
import AreaP from '../components/area.vue'
import { callAppRouter } from '@/js/wjhJS.js'

export default {
  name: 'home',
  data () {
    return {
      top_tips: false,
      changeColor: 'rgba(0, 0, 0, 0.2)',
      showConfirm: false,
      home_height: 0,
      minusDisable: true,
      addDisable: false,
      acceptNumber: 1,
      show: false,
      tips: 2,
      inputData: {
        province: '',
        city: '',
        area: '',
        address: '',
        receiver_name: '',
        qq: '',
        mobile: '',
        count: 1,
        gift_id: 5
      }
    }
  },
  components: {
    AreaP
  },
  methods: {
    showConfirmMethods () {
      if (this.top_tips) {
        return
      }
      for (let i in this.inputData) {
        if (!this.inputData[i]) {
          console.log(i)
          this.top_tips = true
          setTimeout(() => {
            this.top_tips = false
          }, 2000)
          return
        }
      }
      this.showConfirm = true
    },
    toJiFen () {
      callAppRouter('Redirect', {
        'code': 10028
      }, (res, ed) => {
      })
    },
    toHuiYuanCenter () {
      callAppRouter('Redirect', {
        'code': 10017
      }, (res, ed) => {
      })
    },
    alst (desc) {
      /* eslint-disable */
      if (desc.replace(/\s/g, '')) {
        this.changeColor = 'rgba(0, 0, 0, 0.6)'
      }
      return desc.replace(/\s/g, '') ? desc : '选择配送城市和地区'
    },
    showArea () {
      this.show = true
      this.$refs['area'].close(() => {
        this.show = false
      })
    },
    minus () {
      // eslint-disable-next-line
      this.acceptNumber > 1 ? this.acceptNumber -= 1 : ''
    },
    add () {
      // eslint-disable-next-line
      this.acceptNumber < +this.$root.sourceData.gift_count_remain ? this.acceptNumber += 1 : ''
    },
    submit () {
      this.$axios({
        url: '/web/v1/cosleep/point/gift/order',
        method: 'POST',
        data: this.inputData,
        headers: {
          'openid': this.$root.openid || 'b389419558def31fd0adf7b1dd6b7ec4'
        }
      }).then(res => {
        console.log(res)
        this.showConfirm = false
        if (+res.data.status !== 1) {
          // msg
          this.tips = 1
        } else if (+res.data.status === 1) {
          // 成功
          this.tips = 0
        }
      })
    }
  },
  watch: {
    acceptNumber (val, oldVal) {
      (this.acceptNumber <= +this.$root.sourceData.gift_count_remain && this.acceptNumber !== 1) ? this.minusDisable = false : this.minusDisable = true;
      (this.acceptNumber >= 1 && this.acceptNumber !== +this.$root.sourceData.gift_count_remain) ? this.addDisable = false : this.addDisable = true;
      this.inputData.count = val
    }
  },
  mounted () {
  }
}
</script>

